import Authenticated from "@/Layouts/AuthenticatedLayout";
import { PageProps } from "@/types";
import { Head } from "@inertiajs/react";
import { useState } from "react";
import { DateRangePicker } from "@/components/date-range-picker";
import { DataPage } from "./data";
import { FetchProvider } from "@/hooks/FetchContext";

export default function Index({ auth }: PageProps) {
    const role = auth.user.role;
    const [url, setUrl] = useState(`/dashboard/fetch/offers`);
    return (
        <Authenticated user={auth.user}>
            <Head title="Offers">
                <meta
                    name="description"
                    content=" Manage and track your offers in one convenient
                        location."
                />
            </Head>
            <div className="flex justify-between items-center">
                <div>
                    <h2 className="text-lg">Offers</h2>
                    <p className="text-sm text-gray-500 hidden sm:block">
                        Manage and track your offers in one convenient location.
                    </p>
                </div>
                <DateRangePicker
                    endPoint={url}
                    onUrlChange={(url: string) => setUrl(url)}
                />
            </div>

            <FetchProvider url={url}>
                <DataPage url={url} setUrl={setUrl} role={role} />
            </FetchProvider>
        </Authenticated>
    );
}
